.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0); }

* {
  box-sizing: border-box; }

body {
  min-height: 100vh;
  font-style: 16px; }

a {
  text-decoration: none; }

a:active,
a:focus {
  outline: 0;
  -webkit-tap-highlight-color: transparent; }

.social {
  display: flex;
  flex-wrap: wrap; }

.social__link,
.search-button {
  margin: 10px;
  width: 3rem;
  height: 3rem;
  background-color: #1b6568;
  border: solid 3px;
  border-radius: 50%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 2.5rem;
  text-decoration: none;
  color: #822519;
  transition: color 1s;
  cursor: pointer;
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.2); }

.social__link:hover {
  color: #ebe9d7; }

.social__link--square {
  border-radius: 10%;
  color: #ebe9d7; }

.social__link--square:hover {
  border-radius: 10%;
  color: #822519; }

.search-button:hover {
  color: #1b6568;
  background-color: #74715c; }

.layout {
  display: grid;
  grid-template-columns: 1fr minmax(auto, 1200px) 1fr;
  grid-template-rows: minmax(100px, max-content) minmax(auto, auto) 100px;
  grid-row-gap: 1rem;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: repeat;
  background-attachment: fixed;
  min-height: 100vh; }

.layout__header {
  grid-column: 1 / 4;
  grid-row: 1 /2; }

.layout__content-container {
  grid-column: 2 / 3; }

.layout__content-footer {
  grid-column: 1 / 4;
  grid-row: 3 / 4; }

.main-header {
  width: 100%;
  padding: 20px 5% 0px;
  display: flex;
  justify-content: space-between;
  background: linear-gradient(to bottom, #822519 0%, #822519 10%, rgba(255, 255, 255, 0) 10%, rgba(255, 255, 255, 0) 30%, #1b6568 30%, #1b6568 70%, rgba(255, 255, 255, 0) 70%, rgba(255, 255, 255, 0) 90%, #822519 90%, #822519 100%);
  box-shadow: 1px -5px 10px 4px black; }

.main-header__logo-wrapper {
  flex: 1 1 auto;
  max-width: 100%; }

.main-header__logo-image {
  max-width: 100%; }

.main-header__container {
  display: flex;
  margin-left: auto;
  justify-content: center;
  flex-direction: column;
  flex: 1 1 auto; }

.main-header__menu {
  order: 4; }

.main-header__social {
  order: -1;
  margin-left: auto; }

.main-header__search-button {
  margin-right: 50px; }

.main-menu {
  display: flex;
  margin: auto;
  height: 50%;
  transition: transform .2s ease-in-out;
  align-items: flex-start; }

.main-menu__link {
  margin: 5px 10px;
  transition: color 0.5s ease-in;
  font-family: Lobster;
  font-size: 36px;
  color: #ebe9d7;
  text-transform: uppercase;
  text-decoration: none;
  outline: none; }

.main-menu__link:hover {
  color: #822519; }

.hamburger-checkbox,
.hamburger-label {
  position: absolute;
  top: 5px;
  left: calc(100% - 48px);
  z-index: 100;
  display: none;
  cursor: pointer;
  font-size: 48px;
  -webkit-tap-highlight-color: transparent; }

.hamburger-checkbox:checked ~ .main-menu {
  transform: translateX(0); }

.hamburger-checkbox:checked ~ .hamburger-label {
  color: #ebe9d7; }

@media (max-width: 1024px) {
  .hamburger-label {
    display: block; }
  .main-menu {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 400px;
    height: 100%;
    z-index: 10;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.8);
    transform: translateX(-100%); }
  .main-menu__link {
    margin: 1em auto; }
  .main-menu__link:after {
    display: block;
    width: 100%;
    height: inherit;
    position: relative;
    top: 10px;
    border-bottom: 2px solid white;
    content: '  '; } }

.content {
  display: grid;
  grid-template-columns: 1fr 300px;
  grid-gap: 1rem; }

.main-content,
.sidebar-content {
  border-top: 5px solid #822619;
  border-bottom: 5px solid #822619;
  border-radius: 10px;
  padding: 1rem 0;
  grid-gap: 1rem; }

.main-content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, max-content));
  grid-template-rows: repeat(auto-fill, minmax(350px, max-content));
  grid-gap: 0.5rem;
  align-items: flex-start;
  grid-column: 1 / 2;
  grid-row: 1 / 2; }

.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-column: 2 / 3;
  grid-row: 1 / 2; }

.sidebar-content__author {
  grid-column: 1 / 2; }

.author {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.author__info {
  font-family: Lobster;
  text-transform: uppercase;
  margin: 1rem;
  text-align: center; }

.author__photo {
  max-width: 200px; }

.sidebar-content__social {
  justify-content: center;
  margin: 3rem 0; }

@media (max-width: 1200px) {
  .content {
    padding: 1rem; } }

@media (max-width: 840px) {
  .content,
  .sidebar-content {
    display: grid;
    grid-template-rows: 250px 1fr minmax(850px, auto); }
  .main-content {
    grid-row-start: 3;
    grid-column: 1 / span 2; }
  .sidebar-content {
    grid-row: 1 / span 3;
    grid-column: 1 / span 2; }
  .sidebar-content__bottom {
    grid-row: 3 / 4;
    flex-direction: row;
    flex-wrap: wrap; }
  .author {
    flex-direction: row;
    align-items: center; } }

@media (max-width: 540px) {
  .content,
  .sidebar-content {
    display: grid; } }

.card {
  display: flex;
  flex-direction: column;
  background-color: #ebe9d7;
  padding: 5px; }

.card__link {
  color: #822619; }

.card__image-wrapper {
  border: 2px solid #1b6568;
  overflow: hidden; }

.card__photo {
  width: 100%;
  transition: transform 1s ease-out; }

.card__photo:hover {
  transform: scale(1.5); }

.card__heading {
  font-family: Lobster;
  font-size: 1.5rem;
  margin: 0.5rem 0; }

.card__text {
  font-family: 'IM Fell French Canon', serif;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-bottom: 1rem; }

.card__details {
  display: flex;
  justify-content: space-between;
  font-weight: bold; }

.card__details span:hover {
  color: #962d1e; }

.form {
  margin: 2rem 1rem;
  padding: 1rem;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  background-color: #1b6568;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4); }

.form__search {
  flex-direction: row;
  align-items: baseline; }

.form__input {
  margin-bottom: 0.5rem;
  padding: 5px;
  border: 3px solid #ebe9d7;
  font-family: 'IM Fell French Canon', serif;
  font-size: 18px;
  border: 2px solid #1b6568; }

.form__input--search {
  width: 100px;
  flex-grow: 2;
  margin-right: 0.5rem; }

.form__input:focus {
  border: 2px solid #ebe9d7; }

.form__button {
  margin: 2rem;
  padding: 0.5rem;
  color: #822619;
  background: #ebe9d7;
  border: #822619;
  border-bottom: 3px solid #ebe9d7; }

.form__button--search {
  margin: 0.5rem 0; }

.form__button--subscribe:before {
  margin-right: 1rem; }

.form__button:hover {
  border-bottom: 3px solid #822619; }

.tags {
  text-align: center;
  margin: 2rem; }

.tags__link {
  display: inline-block;
  margin: 0.2rem;
  padding: 0.2em 0.5em;
  font-size: 20px;
  color: white;
  background-color: #1b6568;
  border-radius: 5px; }

.tags__link::before {
  margin-right: 0.2em; }

.commentaries {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.user-commentaries {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  margin: 1rem; }

.user-photo-container {
  max-width: 100px;
  min-width: 50px;
  margin-right: 0.5rem; }

.user-photo-image {
  max-width: 100%;
  height: auto; }

.user-commentaries__content {
  display: flex;
  flex-direction: column; }

.user-name {
  font-family: Lobster;
  font-size: 24px/1.2; }

.user-comment {
  margin-top: 1rem;
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

@media (max-width: 1024px) {
  .commentaries {
    flex-direction: row;
    flex-wrap: wrap; } }

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, rgba(130, 37, 25, 0) 0%, rgba(255, 255, 255, 0) 30%, #1b6568 30%, #1b6568 70%, rgba(255, 255, 255, 0) 70%, rgba(255, 255, 255, 0) 100%);
  color: white;
  font-family: 'IM Fell French Canon', serif;
  font-style: italic;
  font-size: 20px; }
